@import "sass/index";

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 80px 24px;
}
.innerWrapper {
  display: flex;
  flex-direction: column;

  max-width: calc(100% - 48px);
  width: 800px;
  text-align: center;
}
.title {
  font-size: 36px;
}
.description {
  font-size: 16px;
}
.link {
  font-size: 16px;
  & a {
    color: use(--secondary-main);
  }
}
.logoWrapper {
  font-size: 16px;
}
.logo {
  margin: 0 auto;
  margin-top: spacing(6);
  max-width: 300px;
  background-position: center;
}
.buttonWrapper {
  margin-top: spacing(6);
}
.button {
}
